  line-height: 22px;
}

.subscription__terms {
  font-size: 13px;
  line-height: 16px;
  font-weight: 400;
  color: #c5c5c5;
}
.subscription__terms a {
  text-decoration: underline;
  color: #c5c5c5;
}
.subscription__terms a:hover, .subscription__terms a:focus {
  color: #ffde40;
}
.subscription__terms a:active {
  color: #ff0000;
}

@media (max-width: 768px) {
  .subscription__container {
    max-width: 99%;
    margin: 0;
    padding: 30px 17px 72px;
  }
  .subscription__buy {
    position: relative;
    max-width: 100%;
    gap: 23px;
  }
  .subscription__buy::after {
    display: none;
  }
  .subscription__buy h2 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -0.64px;
  }
  .subscription__buy p {
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 0;
  }
  .subscription__discount {
    max-width: 100%;
    gap: 23px;
    margin-left: 0;
    margin-bottom: 72px;
  }
  .subscription__discount::before {
    display: none;
  }
  .subscription__discount::after {
    position: absolute;
    content: "";
    background-image: url("../img/subscr-community-mobile.webp");
    background-size: cover;
    width: 375.49px;
    height: 374.33px;
    top: 0;
    left: -33px;
  }
}
@media (max-width: 768px) and (min-resolution: 1.5dppx) {
  .subscription__discount::after {
    background-image: url("../img/subscr-community-mobile@2x.webp");
  }
}
@media (max-width: 768px) {
  .subscription__discount h3 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -0.72px;
    padding-top: 368px;
  }
}
@media (max-width: 768px) {
  .subscription__discount p {
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 5px;
  }
}
@media (max-width: 768px) {
  .subscription__button {
    width: 100%;
    font-size: 18px;
    line-height: 22px;
    min-height: 49px;
  }
}
@media (max-width: 768px) {
  .subscription__terms {
    font-size: 10px;
    line-height: 12px;
  }
}